///
/// Returns a specified z-index
/// @param  {string} $index 		z-index named value or int
/// @param  {int} 		$mod   		defaults to null, adds to the retrieved value
/// @return {int}        			computed z index return value
///
@function z( $index, $mod: null ) {
	$out: 0;
	@if (map-has-key($z-index, $index)) {
		$out: map-get($z-index, $index)
	}
	@else {
		@if type-of($index) != 'number' {
			@warn 'Argument passed to z() is not a number';
			@return $out;
		}
		@else {
			$out: unitless($index);
		}
	}

	@if ($mod != null) {
		$out: $out + $mod;
		@return $out;
	}
	@else {
		@return $out;
	}
}

///
/// The unit function
/// @param  {int} $multiplier		 int or string with math
/// @return {CSS unit}            $u global, multiplied by argument
///
@function u( $multiplier: 1 ) {
	@return $u * $multiplier;
}

///
/// Alias for the font-size portion of
/// @param {}
///
@function fs($size) {
	@return ($size / $font__s-base) * 1rem;
}
